.header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  padding: 40px 0;
  z-index: 5;
  @include tablet {
    padding: 25px 0;
  }
  // .header__container

  &__container {
    display: flex;
    align-items: center;
    @include tablet {
      justify-content: space-between;
    }
  }
}
.logo {
  font-weight: 700;
  font-size: 24px;
  line-height: 29px;
  color: var(--black);
  z-index: 10;
}
.nav {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  @include tablet {
    width: auto;
  }
  // .nav__logo

  &__logo {
    margin-right: 40px;
    z-index: 9999;
    @include mobile {
      margin-right: 8px;
    }
  }

  // .nav__menu

  &__menu {
    display: flex;
    align-items: center;
    @include tablet {
      position: fixed;
      top: 0;
      left: 0;
      width: 100vw;
      height: 100vh;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      transform: translateX(-100%);
      background: var(--light-orange);
      transition: 0.5s all;
      overflow: scroll;
    }
  }

  // .nav__list

  &__list {
    display: flex;
    gap: 40px;
    @include tablet {
      flex-direction: column;
      width: 100%;
      padding: 0 40px;
    }
  }

  // .nav__item

  &__item {
    display: flex;
    align-items: center;
    position: relative;
    transition: 0.5s all;
    @include tablet {
      justify-content: space-between;
      flex-direction: column;
    }
    svg {
      margin-left: 8px;
      width: 15px;
      height: 15px;
      transition: 0.5s;
    }
    &:hover {
      transition: 0.5s;
      svg {
        transform: rotate(-180deg);
        fill: var(--orange);
      }
      .nav__link {
        color: var(--orange);
      }
      .nav__dropdawn {
        display: block;
        visibility: visible;
        opacity: 1;
      }
    }
  }

  // .nav__link

  &__link {
    font-family: "Gilroy";
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 150%;
    color: var(--dark);
    transition: 0.5s;
    z-index: 6;
    cursor: pointer;
    @include tablet {
      font-weight: 700;
      font-size: 34px;
    }
  }
  // .nav__wrapper

  &__wrapper {
    display: flex;
    width: 100%;
    justify-content: space-between;
    align-items: center;
  }

  // .nav__dropdawn

  &__dropdawn {
    transition: 0.5s;
    transform: translateY(5px);
    position: absolute;
    visibility: hidden;
    opacity: 0;
    display: none;
    top: 20px;
    left: 0;
    min-width: 190px;
    background: #f9f1e7;
    padding: 10px 15px;
    z-index: 1;
    @include tablet {
      position: static;
      width: 100%;
    }
  }

  // .nav__dropdawn-item

  &__dropdawn-item {
    display: flex;
    @include tablet {
      &:not(:first-child) {
        margin-top: 30px;
      }
    }
  }

  // .nav__dropdawn-link

  &__dropdawn-link {
    font-weight: 500;
    font-size: 16px;
    line-height: 150%;
    color: var(--dark);
    transition: 0.5s;
    &:hover {
      color: var(--orange);
    }
    @include tablet {
      font-size: 17px;
    }
  }

  // .nav__user

  &__user {
    display: flex;
    align-items: center;
    gap: 32px;
  }

  // .nav__heart

  &__heart {
    svg {
      width: 20px;
      height: 18px;
      background: none;
      fill: var(--black);
    }
  }

  // .nav__cart

  &__cart {
    svg {
      width: 24px;
      height: 24px;
      background: none;
      fill: var(--black);
    }
  }

  // .nav__avatar

  &__avatar {
    width: 40px;
    height: 40px;
  }
}
.form {
  width: 473px;
  background: var(--light);
  margin: 0 40px 0 20px;
  @media (max-width: 1200px) {
    width: 270px;
  }
  @include tablet {
    margin: 45px 0 0 0;
  }
  // .form__label

  &__label {
    display: flex;
    align-items: center;
    position: relative;
    padding: 0 0 0 44px;
    width: 100%;
    &::before {
      content: url(../img/search.svg);
      position: absolute;
      left: 15px;
      padding: 0;
      margin-right: 12px;
      width: 20px;
      height: 20px;
      z-index: 4;
      text-align: center;
    }
  }

  // .form__input

  &__input {
    width: 100%;
    font-weight: 400;
    font-size: 14px;
    line-height: 150%;
    color: var(--dark);
    height: 45px;
    padding-right: 20px;
    outline: none;
  }
}
.burger {
  @include burger;
  display: none;
  margin-left: 20px;
  z-index: 99999;
  @include tablet {
    display: block;
  }
}
.menu--active {
  transform: translateX(0);
}
