.tips {
  padding: 45px 0;
  background: var(--light);

  // .tips__container

  &__container {}

  // .tips__title

  &__title {
    margin: 0;
    font-weight: 700;
    font-size: 40px;
    line-height: 120%;
    text-align: center;
    color: var(--dark);

  }

  // .tips__sliders

  &__sliders {
    position: relative;
  }

  // .tips__slider

  &__slider {
    margin: 0 -32px;
    padding: 32px;
    min-width: 0;
  }

  // .tips__slide

  &__slide {
    transition: .4s;

    &:hover {
      transition: .4s;
      box-shadow: 0 0 32px rgba(97, 97, 97, 0.2);
    }
  }



  // .tips__slider-body

  &__slider-body {
    width: 100%;

  }

  // .tips__slider-img

  &__slider-img {
    img {
      width: 100%;
    }
  }

  // .tips__slider-content

  &__slider-content {
    padding: 16px 32px 16px 16px;
  }

  // .tips__slider-title

  &__slider-title {
    margin: 0;
    font-weight: 600;
    font-size: 24px;
    line-height: 120%;
    color: var(--dark);
    margin-bottom: 8px;
  }

  // .tips__slider-data

  &__slider-data {
    font-weight: 400;
    font-size: 14px;
    line-height: 150%;
    color: var(--gray);
  }



  // .tips-slider__dots

  &-slider__dots {
    justify-content: center;
  }
}
