.swiper-pagination-bullets {
  display: flex;
  flex-wrap: wrap;
}

.swiper-pagination-bullet {
  cursor: pointer;
  flex: 0 0 27px;
  height: 27px;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: transparent;
  opacity: 1;
  gap: 12px;

  &::after {
    content: "";
    width: 11px;
    height: 11px;
    background: #d8d8d8;
    border-radius: 50%;
    transition: 0.3s;
  }

  &::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    border-radius: 50%;
    border: 1px solid #e89f71;
    transform: scale(0);
    transition: 0.3;
  }
}

.swiper-pagination-bullet-active {
  &::after {
    background: #e89f71;
  }

  &::before {
    transform: scale(1);
  }
}
