.share {
  padding: 45px 0;

  // .share__up-title

  &__up-title {
    margin: 0;
    display: block;
    font-weight: 600;
    font-size: 20px;
    line-height: 150%;
    text-align: center;
    color: var(--dark);
    margin-bottom: 8px;
  }

  // .share__title

  &__title {
    margin: 0;
    font-weight: 700;
    font-size: 40px;
    line-height: 120%;
    color: var(--dark);
    text-align: center;
  }

  //.share__body
  &__body {
    position: relative;

    @media (any-hover: none) {
      overflow: auto;
      -webkit-overflow-scrolling: touch;
    }

    @include small-tablet {
      overflow: hidden;
    }
  }

  // .share__content

  &__content {
    display: grid;
    grid-template-columns: auto;
    grid-auto-flow: column;
    align-items: center;
    gap: 16px;

    @media (any-hover: hover) {
      justify-content: center;
    }

    @include small-tablet {
      grid-auto-flow: row;
    }
  }

  // .share__colum

  &__colum {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    margin: 0px -8px;

    &:first-child {
      justify-content: flex-end;
    }
  }

  // .share__row

  &__row {
    display: inline-grid;
    padding: 0px 8px;
    gap: 16px;
    grid-template-columns: auto;
    grid-auto-flow: column;
    width: 100%;

    &:not(:last-child) {
      margin: 0px 0px 16px 0px;
    }

    // .share__row-item
    &-item {
      img {
        width: 100%;
        transition: .3s;
        filter: grayscale(1);
      }

      &:hover {
        img {
          filter: grayscale(0);
        }
      }
    }

    // .share__row-top
    &-top {
      align-items: flex-end;
    }

    // .share__row-bottom
    &-bottom {
      align-items: flex-start;
    }
  }
}
