.products {
  padding: 70px 0;
  // .products__title

  &__title {
    margin: 0;
    font-weight: 700;
    font-size: 40px;
    line-height: 120%;
    text-align: center;
    color: var(-dark);
    margin-bottom: 32px;
  }

  // .products__col

  &__col {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(285px, 1fr));
    gap: 32px;
  }

  // .products__item

  &__item {
    display: flex;
    flex-direction: column;
    position: relative;
    height: 100%;

    &:hover,
    :hover.products__overlay {
      opacity: 1;
    }

    &:nth-child(n + 5) {
      display: none;
    }

    &.is-visible {
      display: block;
    }
  }

  //.product__body
  &__body {
    height: 100%;
  }

  // .products__label

  &__label {
    position: absolute;
    top: 24px;
    right: 24px;
    width: 48px;
    height: 48px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 500;
    font-size: 16px;
    line-height: 150%;
    color: var(--light);
  }

  // .products__img

  &__img {
    width: 100%;

    img {
      width: 100%;
    }
  }

  // .products__content

  &__content {
    padding: 16px 31px 30px 16px;
    background: var(--light-orange);
  }

  // .products__content-title

  &__content-title {
    margin: 0;
    font-weight: 600;
    font-size: 24px;
    line-height: 120%;
    color: var(--dark);
    margin-bottom: 8px;
  }

  // .products__content-descr

  &__content-descr {
    margin: 0;
    font-weight: 500;
    font-size: 16px;
    line-height: 150%;
    color: var(--gray);
    margin-bottom: 8px;
  }

  // .products__content-prices

  &__content-prices {
    display: flex;
    align-items: center;
    gap: 16px;
  }

  // .products__content-price

  &__content-price {
    font-weight: 600;
    font-size: 20px;
    line-height: 150%;
    color: var(--dark);
  }

  // .products__content-old__price

  &__content-old__price {
    font-weight: 400;
    font-size: 16px;
    line-height: 150%;
    text-decoration-line: line-through;
    color: var(--gray);
  }


  // .products__overlay

  &__overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #3A3A3A;
    opacity: 0.72;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    transition: .3s;
    opacity: 0;
  }

  // .products__overlay-link

  &__overlay-link {
    max-width: 202px;
    width: 100%;
    padding: 12px;
    text-align: center;
    background: var(--light);
    font-weight: 600;
    font-size: 16px;
    line-height: 150%;
    color: var(--orange);
    margin-bottom: 24px;
    transition: .3s;

    &:hover {
      color: var(--light);
      background: var(--orange);
    }
  }

  //.products__ovelray-wrapper
  &__ovelray-wrapper {
    display: flex;
    gap: 48px;
  }

  // .products__overlay-share

  &__overlay-share {
    display: flex;
    align-items: center;
    font-weight: 600;
    font-size: 16px;
    line-height: 150%;
    color: var(--light);

    &::before {
      content: url(../img/share.svg);
      width: 24px;
      height: 24px;
      margin-right: 8px;
    }
  }

  // .products__overlay-like

  &__overlay-like {
    display: flex;
    align-items: center;
    font-weight: 600;
    font-size: 16px;
    line-height: 150%;
    color: var(--light);

    &::before {
      content: url(../img/like.svg);
      width: 24px;
      height: 24px;
      margin-right: 8px;
    }
  }


  //.products-label-price

  &-label-price {
    background: #E97171;
  }

  //.products-label-new
  &-label-new {
    background: #2EC1AC;
  }


  // .products__show-more

  &__show-more {
    display: flex;
    justify-content: center;
  }

  // .products__show-item

  &__show-item {
    margin: 0 auto;
    display: inline-block;
    margin-top: 32px;
    max-width: 245px;
    width: 100%;
    padding: 12px;
    text-align: center;
    background: var(--light);
    font-weight: 600;
    font-size: 16px;
    line-height: 150%;
    color: var(--orange);
    margin-bottom: 24px;
    transition: .3s;
    border: 1px solid #E89F71;

    &:hover {
      color: var(--light);
      background: var(--orange);
    }
  }


}
