.hero {
  padding: 140px 0 0 0;
  background-color: var(--light-grayish);
  position: relative;
  //.hero__container

  &__container {
    display: flex;

    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 60%;
      height: 100%;
      background: #f9f1e7;
    }

    @include tablet {
      flex-direction: column;
    }
  }

  // .hero__content

  &__content {
    max-width: 494px;
    background: rgba(255, 255, 255, 0.9);
    padding: 60px;
    z-index: 3;
    margin-bottom: 80px;
    align-self: flex-start;

    @include tablet {
      max-width: none;
      padding: 20px;
    }
  }

  // .hero__title

  &__title {
    margin: 0;
    font-weight: 700;
    font-size: 56px;
    line-height: 120%;
    color: var(--dark);
    margin-bottom: 16px;

    @include mobile {
      font-size: 35px;
    }
  }

  // .hero__text

  &__text {
    margin: 0;
    font-weight: 500;
    font-size: 20px;
    line-height: 150%;
    color: var(--gray);
    margin-bottom: 48px;
  }

  // .hero__btn

  &__btn {
    display: inline-block;
    background: var(--orange);
    width: 100%;
    text-align: center;
    padding: 24px 5px;
    font-weight: 600;
    font-size: 20px;
    line-height: 150%;
    color: var(--light);
  }

  // .hero__slider

  &__slider {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    min-width: 0;
    align-self: flex-end;

    @include tablet {
      flex-direction: column-reverse;
      align-self: stretch;
    }
  }

  // .hero__control

  &__control {
    display: flex;
    justify-content: space-between;
    align-items: center;
    z-index: 3;
    align-self: stretch;
    margin-bottom: 22px;

    @include tablet {
      margin-top: 50px;
    }
  }

  // .hero__arrow

  &__arrow {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 8px;
  }

  // .hero__arrow-prev

  &__arrow-prev {
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 48px;
    height: 48px;
    background: #e89f71;
    opacity: 0.3;
    border-radius: 50%;
    transition: 0.5s;

    svg {
      transform: rotate(90deg);
      width: 24px;
      height: 24px;
    }

    &:hover {
      opacity: 1;

      svg {
        fill: var(--light);
      }
    }
  }

  // .hero__aroow-next

  &__arrow-next {
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 48px;
    height: 48px;
    background: #e89f71;
    opacity: 0.3;
    border-radius: 50%;
    transition: 0.5s;

    svg {
      transform: rotate(-90deg);
      width: 24px;
      height: 24px;
    }

    &:hover {
      opacity: 1;

      svg {
        fill: var(--light);
      }
    }
  }

  // .hero__sliders

  &__sliders {
    overflow: visible;
    z-index: 1;

    @include tablet {
      overflow: hidden;
    }
  }

  // .hero__slider-body

  &__slider-body {
    position: relative;
    max-width: 100%;
    height: 100%;
  }

  // .hero__slider-img

  &__slider-img {
    height: 100%;

    img {
      width: 100%;
      height: 100%;
    }
  }

  // .hero__slider-content

  &__slider-content {
    position: absolute;
    right: 40px;
    bottom: 40px;
    background: rgba(255, 255, 255, 0.72);
    backdrop-filter: blur(15.5px);
    max-width: 295px;
    width: 100%;
    padding: 24px;

    @include mobile {
      right: 0px;
      bottom: 0;
      max-width: 170px;
    }
  }

  // .hero__slider-title

  &__slider-title {
    margin: 0;
    font-weight: 600;
    font-size: 28px;
    line-height: 120%;
    color: var(--dark);
    margin-bottom: 4px;
  }

  // .hero__slider-text

  &__slider-text {
    margin: 0;
    font-weight: 500;
    font-size: 16px;
    line-height: 150%;
    color: var(--dark);
    margin-bottom: 8px;
  }

  // .hero__slider-price

  &__slider-price {
    margin: 0;
    font-weight: 600;
    font-size: 20px;
    line-height: 150%;
    color: var(--dark);
  }

  .swiper-body {
    width: 934px;
    height: 553px;

    @media (max-width: 1595px) {
      width: 750px;
    }

    @media (max-width: 1255px) {
      width: 500px;
    }

    @include mobile {
      width: 290px;
      height: 450px;
    }
  }
}
