.benefits {
  padding: 85px 0;

  @include mobile {
    padding: 20px 0;
  }

  // .benefits__body

  &__body {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    gap: 90px;

    @include mobile {
      gap: 20px;
      flex-direction: column;
      justify-content: flex-start;
      align-items: flex-start;
    }
  }

  // .benefits__item

  &__item {
    display: flex;
    align-items: center;
    justify-content: center;
    max-width: 280px;
    padding: 15px 0;

    @include mobile {
      justify-content: flex-start;
      align-items: flex-start;
    }
  }

  // .benefits__content
  &__content {
    margin-left: 25px;
  }

  // .benefits__title

  &__title {
    margin: 0;
    font-weight: 600;
    font-size: 18px;
    line-height: 150%;
    color: var(--dark);
    margin-bottom: 2px;

    @include mobile {
      font-size: 16px;
    }
  }

  // .benefits__text

  &__text {
    margin: 0;
    font-weight: 500;
    font-size: 16px;
    line-height: 150%;
    color: var(--gray);

    @include mobile {
      font-size: 14px;
    }
  }
}
