.rooms {
  padding: 45px 0;
  background: var(--light-grayish);
  // .rooms__container

  &__container {
    display: flex;
    align-items: center;

    @include tablet {
      flex-direction: column;
    }
  }

  // .rooms__content

  &__content {
    flex: 0 0 30%;
    margin-right: 86px;
    position: relative;
    z-index: 15;
  }

  // .rooms__title

  &__title {
    margin: 0;
    font-weight: 700;
    font-size: 40px;
    line-height: 120%;
    color: var(--dark);
    margin-bottom: 8px;
  }

  // .rooms__text

  &__text {
    margin: 0;
    font-weight: 500;
    font-size: 16px;
    line-height: 150%;
    color: var(--dark);
    margin-bottom: 24px;
  }

  // .rooms__btn

  &__btn {
    display: flex;
    max-width: 176px;
    width: 100%;
    align-items: center;
    justify-content: center;
    padding: 12px 0;
    background: #E89F71;
    font-weight: 600;
    font-size: 16px;
    line-height: 150%;
    color: var(--light);
  }



  // .rooms__sliders

  &__sliders {
    flex: 1 1 auto;
    min-width: 0px;
    align-self: flex-end;
    position: relative;

    @include tablet {
      margin-top: 50px;
    }
  }

  // .rooms__slider

  &__slider {
    overflow: visible;
    position: relative;

    &::before {
      content: "";
      position: absolute;
      top: 0;
      height: 100%;
      width: 100vw;
      left: 0;
      background-color: #fcf8f3;
      z-index: 2;
      transform: translate(-100%, 0px);
    }

  }

  // .rooms__slide
  &__slide {
    max-width: 486px;

  }

  // .rooms__body

  &__body {
    width: 100%;
    overflow: hidden;
    max-height: 582px;
    transition: height .5s;
  }


  // .rooms__slider-img

  &__slider-img {
    width: 100%;
    height: 486px;
    transition: height .5s;

    @include small-tablet {
      height: auto;
    }

    img {
      width: 100%;
      height: 100%;
      z-index: 5;
      transition: height .5s;

      @include small-tablet {
        height: auto;
      }
    }
  }



  // .rooms__slider-content

  &__slider-content {
    position: absolute;
    bottom: 24px;
    left: 24px;
    padding: 32px;
    background: rgba(255, 255, 255, 0.72);
    backdrop-filter: blur(1.5px);
    max-width: 217px;
    transition: .3;

    @include mobile {
      left: 5px;
      bottom: 15px;
    }

    &::after {
      content: url(../img/arrow.svg);
      width: 48px;
      height: 48px;
      position: absolute;
      bottom: 0;
      right: -48px;
      display: flex;
      justify-content: center;
      align-items: center;
      background: #E89F71;
    }
  }

  // .rooms__slider-title

  &__slider-title {
    margin: 0;
    display: flex;
    align-items: center;
    gap: 8px;
    font-weight: 500;
    font-size: 16px;
    line-height: 150%;
    color: var(--dark);

    span {
      width: 27px;
      border: 1px solid #616161;
    }
  }

  // .rooms__slider-text

  &__slider-text {
    margin: 0;
    font-weight: 600;
    font-size: 28px;
    line-height: 120%;
    color: var(--dark);
  }

  // .rooms__slider-dots

  &__slider-dots {
    position: absolute;
    bottom: 45px !important;
    left: 515px !important;
    z-index: 15;

    @include small-tablet {
      position: static;
      margin-top: 20px;
    }
  }

  .swiper-slide-active {
    .rooms__slider-img {
      height: 100%;
      transition: height .5s;
    }
  }
}
