.footer {
  padding: 30px 0;
  // .footer__container

  &__container {
    border-top: 1px solid #D8D8D8;
    padding-top: 80px;
  }

  // .footer__content

  &__content {
    display: flex;
    justify-content: space-between;
    gap: 75px;
    flex-wrap: wrap;
  }

  // .footer__list

  &__list {
    max-width: 250px;
    display: flex;
    flex-direction: column;
    gap: 16px;
  }

  //.footer__info
  &__info {
    display: flex;
    flex-direction: column;
    gap: 16px;
    max-width: 249px;

    @include small-tablet {
      max-width: none;
    }
  }

  // .footer__text

  &__text {
    font-weight: 400;
    font-size: 16px;
    line-height: 150%;
    color: var(--gray);
  }

  // .footer__loc

  &__loc {
    display: flex;
    align-items: center;
    font-weight: 400;
    font-size: 16px;
    line-height: 150%;
    color: var(--gray);

    &::before {
      content: url(../img/map.svg);
      width: 20px;
      height: 20px;
      margin-right: 8px;
    }
  }

  // .footer__tel

  &__tel {
    display: flex;
    align-items: center;
    font-weight: 400;
    font-size: 16px;
    line-height: 150%;
    color: var(--gray);

    &::before {
      content: url(../img/tel.svg);
      width: 20px;
      height: 20px;
      margin-right: 8px;
    }
  }

  // .footer__web

  &__web {
    font-weight: 400;
    font-size: 16px;
    line-height: 150%;
    color: var(--gray);
  }

  // .footer__title

  &__title {
    margin: 0;
    font-weight: 700;
    font-size: 24px;
    line-height: 29px;
    color: var(--black);
  }

  // .footer__menu

  &__menu {
    display: flex;
    flex-direction: column;
    gap: 16px;
  }

  // .footer__link

  &__link {
    font-weight: 500;
    font-size: 16px;
    line-height: 150%;
    color: var(--gray);
    transition: .5s;

    &:hover {
      color: var(--orange);
    }

  }

  // .footer__form

  &__form {
    display: flex;
    gap: 4px;
  }



  // .footer__input

  &__input {
    padding: 10px 12px;
    background: #F4F5F7;
    outline: none;
    height: 100%;
  }

  // .footer__btn

  &__btn {
    background: #E89F71;
    padding: 10px 11px;

    svg {
      width: 24px;
      height: 24px;
      fill: var(--light);
    }
  }
}
